<template>
<v-flex ma-5>

    <v-row>
        <v-col cols="12" md="5">

            <v-card class="mb-5" max-height="850">
                <v-card-title class="headline blue-grey lighten-1 white--text" primary-title>
                    Route List
                    <v-spacer></v-spacer>
                    <v-btn @click="loadRoutes" color="green" dark>Load New Routes</v-btn>
                </v-card-title>
                <v-simple-table fixed-header height="100%">
                    <template v-slot:default>
                        <thead style="overflow-y: scroll;overflow-x: hidden;">
                            <tr>
                                <th width="10%" class="text-left">ID</th>
                                <th width="30%" class="text-left">Name</th>
                                <th width="30%" class="text-left">AssignedTo</th>
                                <th class="text-left">Action</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="item in routes" :key="item.routeId">

                                <td @click="selectedRowItem(item)">
                                    {{ item.routeId }}
                                </td>
                                <td @click="selectedRowItem(item)">{{ item.route }}</td>
                                <td @click="selectedRowItem(item)">{{ item.userName }}</td>
                                <td>
                                    <v-layout row>
                                        <v-btn icon @click="deleteItem(item)">
                                            <v-icon color="red">delete</v-icon>
                                        </v-btn>
                                        <v-btn icon @click="openAssigner(item)" v-if="item.status != 7">
                                            <v-icon color="blue">account_circle</v-icon>
                                        </v-btn>

                                    </v-layout>

                                </td>
                            </tr>
                        </tbody>
                    </template>
                </v-simple-table>
            </v-card>

        </v-col>
        <v-col cols="12" md="7">

            <v-card hover class="mb-12" max-height="850">
                <v-card-title class="headline blue-grey lighten-1 white--text" primary-title>
                    Routes : {{selectedItem}} Address List
                    <v-spacer></v-spacer>

                </v-card-title>

                <v-simple-table fixed-header height="100%">
                    <template v-slot:default>
                        <thead style="overflow-y: scroll;overflow-x: hidden;">
                            <tr>
                                <th class="text-left">firstName</th>

                                <th class="text-left">address</th>
                                <th class="text-left">meterNumber</th>

                                <th class="text-left">Action</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="item in addressData" :key="item.item">
                                <td>
                                    {{ item.clientName }}
                                </td>

                                <td>
                                    {{ item.address }}
                                </td>
                                <td>{{ item.meterNumber }}</td>

                                <td>
                                    <v-btn icon @click="deleteItem(item)">
                                        <v-icon color="red">delete</v-icon>
                                    </v-btn>
                                </td>
                            </tr>
                        </tbody>
                    </template>
                </v-simple-table>
            </v-card>

        </v-col>

    </v-row>

    <v-dialog v-model="dialogEditItem" width="500">

        <v-card>
            <v-card-title class="headline grey lighten-2" primary-title>
                User Selection

            </v-card-title>
            <v-flex ma-3>
                <v-combobox v-model="selectedUser" :items="users" item-text="userName" hide-selected label="Select user type"></v-combobox>

                <v-btn @click="assign" large block color="primary">Assign User to route</v-btn>
            </v-flex>
            <v-card-text>

            </v-card-text>

        </v-card>
    </v-dialog>

    <v-dialog v-model="dialogloader" hide-overlay persistent width="300">
        <v-card color="primary" dark>
            <v-card-text>
                <br>
                <h3>Please stand by</h3>
                <br>
                <v-progress-linear indeterminate color="white" class="mb-0"></v-progress-linear>
                <br>
            </v-card-text>
        </v-card>
    </v-dialog>

    <v-dialog v-model="dialogMap" fullscreen hide-overlay transition="dialog-bottom-transition">

        <v-card>
            <v-toolbar dark color="#2B5563">
                <v-toolbar-title>Addresses on routes</v-toolbar-title>
                <v-spacer></v-spacer>
                <v-toolbar-items>
                    <v-btn dark text @click="dialogMap = false">Close</v-btn>
                </v-toolbar-items>
            </v-toolbar>

            <v-flex xs12 lg12>
                <div class="map">
                    <MapScreen :mapData="geoData" />
                </div>
            </v-flex>

        </v-card>
    </v-dialog>

    <v-dialog v-model="dialogUpload" width="700">
        <v-card>
            <v-card-title class="headline grey lighten-2" primary-title>
                <span class="headline">Upload CSV File</span>
                <v-spacer></v-spacer>
                <v-btn :loading="loading3" :disabled="loading3" color="primary" class="white--text" @click="$refs.doc.click()">
                    Upload : {{fileName}}
                    <v-icon right dark>cloud_upload</v-icon>
                </v-btn>

                <input type="file" ref="doc" accept=".csv" @change="uploader" style="display: none">
            </v-card-title>
            <v-flex pa-3>

                <v-text-field label="Route Name" outlined type="text" v-model="routeData.route"></v-text-field>
                <v-text-field label="Route ID" outlined type="number" v-model="routeData.routeId"></v-text-field>
                <v-simple-table fixed-header height="200">
                    <template v-slot:default>
                        <thead style="overflow-y: scroll;overflow-x: hidden;">
                            <tr>
                                <th class="text-left">firstName</th>

                                <th class="text-left">address</th>
                                <th class="text-left">meterNumber</th>

                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="item in csvData" :key="item.item">
                                <td>
                                    {{ item.clientName }}
                                </td>

                                <td>
                                    {{ item.address }}
                                </td>
                                <td>{{ item.meterNumber }}</td>
                            </tr>
                        </tbody>
                    </template>
                </v-simple-table>

                <br>
                <v-btn @click="save" large block color="primary" class="mb-5">Save</v-btn>
                <v-btn @click="dialogUpload = false" large block dark color="red">Cancel</v-btn>

            </v-flex>
            <v-card-text>

            </v-card-text>
        </v-card>
    </v-dialog>
</v-flex>
</template>

<script>
import MapScreen from './Map'
import Axios from 'axios-observable'
export default {
    components: {
        MapScreen
    },
    data() {
        return {
            e1: 0,
            select: null,
            geoData: null,
            selectedUser: null,
            dialogUpload: false,
            dialogMap: false,
            dialogEditItem: false,
            users: [],
            dialogloader: false,
            nodeApi: '',
            selectedRoute: null,
            selectedItem: null,
            routes: [],
            addressData: [],
            editedIndex: -1,
            defaultrouteData: {
                route: '',
                routeId: 0,
                createdBy: '',
                address: ''
            },
            routeData: {
                route: '',
                routeId: 0,
                createdBy: '',
                address: ''
            },
            csvData: [],
            loader: null,
            loading3: false,
            files: '',
            fileName: ''
        }
    },
    watch: {
        dialogUpload(val) {
            if (val) {
                this.csvData = []
                this.fileName = ''
                this.files = ''
            }else{
                 this.csvData = []
                this.fileName = ''
                this.files = ''
            }
        },
        dialogEditItem(val) {
            if (val) {
                this.getUsers()
            } else {
                this.editedIndex = -1
            }
        }
    },

    created() {
        //  window.addEventListener('keydown', (e) => {
        //   //  console.log(e.keyCode)
        //   //  console.log(e.key)
        //    this.inputer(e.key)
        //     // if (e.key == 'Escape') {
        //     //   this.showModal = !this.showModal;
        //     // }
        //   });
        this.getRoutes()
    },
    methods: {
        csvReader(fileRaw) {
            return new Promise((resolve, reject) => {
                const reader = new FileReader()
                let csv = '';
                reader.onload = function (e) {
                    csv = e.target.result;
                    resolve(csv);
                }
                reader.onerror = function (err) {
                    reject(err)
                }
                reader.readAsText(fileRaw)
            })
        },
        async uploader(e) {
            let file = null
            file = e.target.files[0]
            this.files = await this.csvReader(file)
            //console.log(file)
            //console.log(this.files)
            this.fileName = file.name

            if (this.files != '') {
                const csv = require('csvtojson')

                csv()
                    .fromString(this.files)
                    .then(json => {
                        this.csvData = json
                        //  console.log(this.csvData)
                        this.$swal.fire({
                            type: 'success',
                            title: 'Document Uploaded!!',
                            text: 'You document was uploaded successfully',
                            showConfirmButton: true
                        })
                    })
                    .catch(err => {
                        this.$swal.fire({
                            type: 'error',
                            title: 'Can not convert csv!!',
                            text: err
                        })
                    })
            }
        },
        save() {
            // console.log(this.routeData.address.length)
            if (this.routeData.route == '') {
                this.$swal.fire(
                    'Route Adding Failed',
                    'Please enter a valid route name',
                    'error'
                )
            } else if (this.routeData.routeId == '') {
                this.$swal.fire(
                    'Route Adding Failed',
                    'Please enter a valid route id',
                    'error'
                )
            } else if (this.csvData.length == 0) {
                this.$swal.fire(
                    'Route Adding Failed',
                    'Please upload a valid route list',
                    'error'
                )
            } else {
                this.dialogloader = true
                this.routeData.createdBy = this.$session.get('user').userdetails.id
                this.routeData.address = JSON.stringify(this.csvData)
                let payload = this.$jwt.sign(this.routeData, 'meterlink@@11', {
                    expiresIn: 15
                })

                let data = {
                    token: this.$session.get('user').token,
                    payload: payload
                }

                Axios.post(this.nodeApi + '/addRoute', data).subscribe(
                    res => {
                        if (res.data.affectedRows) {
                            this.dialogloader = false
                            this.dialogUpload = false
                            this.routeData = this.defaultrouteData
                            this.getRoutes()
                            this.$swal.fire({
                                type: 'success',
                                title: 'Route Saved',
                                text: 'Route was saved successfully'
                            })
                        }
                        if (!res.data.affectedRows) {
                            this.dialogloader = false
                            this.$swal.fire({
                                type: 'error',
                                title: 'User Saving Failed',
                                text: res.data.message
                            })
                        }
                    },
                    err => console.log(err)
                )
            }

        },
        selectedRowItem(data) {
            this.selectedItem = data.route
            //console.log(data)
            this.addressData = JSON.parse(data.address)
        },
        openAssigner(data) {
            this.selectedRoute = data
            this.selectedRoute.address = JSON.stringify(this.selectedRoute.address)
            this.dialogEditItem = true
        },
        async getUsers() {
            let headers = {
                'Content-Type': 'application/json',
                Authorization: 'Bearer ' + this.$session.get('user').token
            }
            Axios.get(this.nodeApi + '/getAllUsers', {
                headers: headers
            }).subscribe(
                res => {
                    this.users = res.data
                },
                err => console.log(err)
            )
        },
        async assign() {
            this.selectedRoute.createdBy = this.$session.get('user').userdetails.id
            this.selectedRoute.assignTo = this.selectedUser.id
            this.selectedRoute.status = 7
            this.selectedRoute.statusApp = 'red'
            this.selectedRoute.icon = 'av-timer'
            let id = this.selectedRoute.id
            delete this.selectedRoute.createdAt
            delete this.selectedRoute.updatedAt
            delete this.selectedRoute.id
            delete this.selectedRoute.userName
            let dataobj = {
                stack: this.selectedRoute,
                stack_id: id,
                assignTo: this.selectedRoute.assignTo
            }
            //  console.log(dataobj)
            this.dialogloader = true

            let payload = this.$jwt.sign(dataobj, 'meterlink@@11', {
                expiresIn: 15
            })
            let data = {
                token: this.$session.get('user').token,
                payload: payload
            }

            await Axios.post(this.nodeApi + '/assignRoute', data).subscribe(
                res => {
                    if (res.data.affectedRows) {
                        this.dialogloader = false
                        this.getRoutes()
                        this.dialogEditItem = false
                        this.$swal.fire({
                            type: 'success',
                            title: 'Route Assignment',
                            text: 'Route Assignment was successfully'
                        })
                    }
                    if (!res.data.affectedRows) {
                        this.dialogloader = false
                        this.$swal.fire({
                            type: 'error',
                            title: 'Route Assignment Failed',
                            text: res.data.message
                        })
                    }
                },
                err => console.log(err)
            )
        },
        async getRoutes() {
            let headers = {
                'Content-Type': 'application/json',
                Authorization: 'Bearer ' + this.$session.get('user').token
            }
            Axios.get(this.nodeApi + '/getAllRoutes', {
                headers: headers
            }).subscribe(
                res => {
                    this.routes = res.data
                },
                err => console.log(err)
            )
        },
        loadRoutes() {
            this.dialogUpload = true
            //  this.getRoutes()
        },

        updateItem() {
            if (this.editedIndex > -1) {
                Object.assign(this.itemData[this.editedIndex], this.newItem)
                this.dialogEditItem = false
            } else {
                this.itemData.push({
                    quantity: this.newItem.quantity,
                    item: this.newItem.item,
                    unitprice: this.newItem.unitprice
                })
                this.dialogEditItem = false
            }
        },
        editItem(item) {
            this.editedIndex = this.itemData.indexOf(item)
            this.newItem = Object.assign({}, item)
            this.dialogEditItem = true
        },
        async deleteItem(item) {
           
            this.dialogloader = true

            let dataobj = {
                routeId: item.routeId,

            }
            let payload = this.$jwt.sign(dataobj, 'meterlink@@11', {
                expiresIn: 15
            })
            let data = {
                token: this.$session.get('user').token,
                payload: payload
            }

            await Axios.post(this.nodeApi + '/deleteRoute', data).subscribe(
                res => {
                    if (res.data.affectedRows) {
                        this.dialogloader = false

                        const index = this.routes.indexOf(item)
                        this.routes.splice(index, 1)

                        this.$swal.fire({
                            type: 'success',
                            title: 'Route Deleted',
                            text: 'Route Assignment was successfully'
                        })
                    }
                    if (!res.data.affectedRows) {
                        this.dialogloader = false
                        this.$swal.fire({
                            type: 'error',
                            title: 'Route Deleting Failed',
                            text: res.data.message
                        })
                    }
                },
                err => console.log(err)
            )

            if(item.assignTo != 0){
                
                 console.log(item)
                 let dataobj = {
                        route: item.route,

                    }
                    let payload = this.$jwt.sign(dataobj, 'meterlink@@11', {
                        expiresIn: 15
                    })
                    let data = {
                        token: this.$session.get('user').token,
                        payload: payload
                    }

                  await Axios.post(this.nodeApi + '/deleteRouteAssigned', data).subscribe(
                    res => {
                    console.log(res.data)
                    },
                    err => console.log(err)
            )
            }
        },
        mapScreen(item) {
            //console.log(item)
            this.geoData = item
            this.dialogMap = true
        }
    }
}
</script>

<style scoped>
.map {
    height: 90vh;
}

.block {
    height: 50%;
}

.container {
    padding-left: 15%;
    padding-right: 15%;
}

.inputfield {
    width: 70%;
    height: 50px;
    text-align: end;
    font-size: 20px;
}

.inputfield2 {
    width: 70%;
    height: 50px;
    text-align: end;
    font-size: 15px;
}

.my-custom-scrollbar {
    position: relative;
    height: 780px;
    width: auto;
    overflow-y: scroll;
    overflow-x: hidden;
}

.scroll-y {
    display: block;
}
</style>
