<template>
<v-flex ma-5>

<v-row>
  <v-col
        cols="12"
        md="4"
      >

            <v-card
                class="mb-5"
                max-height="850"
              >
              <v-card-title
                    class="headline blue-grey lighten-1 white--text"
                    primary-title

                    >
                    Assigned Route List
                  <v-spacer></v-spacer>
                   <v-btn color="green" dark @click="dialogEditItem = true">Select User</v-btn>
              </v-card-title>
              <v-simple-table fixed-header height="100%">
                <template v-slot:default>
                  <thead style="overflow-y: scroll;overflow-x: hidden;">
                    <tr>
                      <th width="10%" class="text-left">ID</th>
                      <th width="30%" class="text-left">Name</th>
                      <th width="30%" class="text-left">Status</th>
                      <th class="text-left">Action</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="item in routes" :key="item.routeId">

                      <td @click="selectedRowItem(item)">
                           {{ item.routeId }}
                      </td>
                      <td @click="selectedRowItem(item)">{{ item.route }}</td>
                      <td @click="selectedRowItem(item)">{{ item.name }}</td>
                      <td>
                        <v-layout row>
                            <v-btn icon @click="deleteItem(item)">
                          <v-icon color="red">delete</v-icon>
                        </v-btn>

                        </v-layout>

                      </td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
        </v-card>




      </v-col>
      <v-col
        cols="12"
        md="8"
      >

        <v-card
                hover
                class="mb-12"

                max-height="850"
              >
              <v-card-title
                    class="headline blue-grey lighten-1 white--text"
                    primary-title
                    >
                   Routes : {{selectedItem}} Address List
                   <v-spacer></v-spacer>

              </v-card-title>

              <v-simple-table fixed-header height="100%">
                <template v-slot:default>
                  <thead style="overflow-y: scroll;overflow-x: hidden;">
                    <tr>

                      <th class="text-left">meterNumber</th>
                      <th class="text-left">Reading</th>
                      <th class="text-left">No-Access</th>
                      <th class="text-left">Notes</th>
                      <th class="text-left">location</th>
                      <th width="20%" class="text-left">Date Time</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="item in addressData" :key="item.item">

                      <td>{{ item.meterNumber }}</td>
                       <td>{{ item.reading }}</td>
                        <td>{{ item.noAccess }}</td>
                        <td>{{ item.notes }}</td>
                      <td >
                          <v-btn icon @click="deleteItem(item)">
                            <v-icon color="green">location_on</v-icon>
                          </v-btn>
                      </td>

                      <td>{{ Date(item.dateTime) }}</td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
          </v-card>

      </v-col>

    </v-row>

    <v-dialog
                v-model="dialogEditItem"
                width="500"
                >

                <v-card>
                    <v-card-title
                    class="headline grey lighten-2"
                    primary-title
                    >
                    User Selection

                    </v-card-title>
                       <v-flex ma-3>
                         <v-combobox
                          v-model="selectedUser"
                          :items="users"
                           item-text="userName"
                           hide-selected
                          label="Select user type"
                        ></v-combobox>

                       <v-btn @click="assign" large block color="primary">Assign User to route</v-btn>
                    </v-flex>
                    <v-card-text>

                    </v-card-text>


                </v-card>
                </v-dialog>

<v-dialog v-model="dialogloader" hide-overlay persistent width="300">
            <v-card color="primary" dark>
                <v-card-text>
                    <br>
                   <h3>Please stand by</h3>
                    <br>
                    <v-progress-linear indeterminate color="white" class="mb-0"></v-progress-linear>
                    <br>
                </v-card-text>
            </v-card>
    </v-dialog>
</v-flex>

</template>
<script>
import Axios from 'axios-observable'
  export default {
    data () {
      return {
        e1: 0,
        select:null,
        selectedUser:null,
        dialogEditItem:false,
        users:[],
        dialogloader:false,
        nodeApi: '',
        selectedRoute: null,
        selectedItem: null,
        routes: [],
        addressData:[],
        editedIndex: -1,

      }
    },
    watch: {
        dialogEditItem(val){
          if(val){
            this.getUsers()
          }else{
            this.editedIndex = -1
          }
        }
    },

    created() {
      //  window.addEventListener('keydown', (e) => {
      //   //  console.log(e.keyCode)
      //   //  console.log(e.key)
      //    this.inputer(e.key)
      //     // if (e.key == 'Escape') {
      //     //   this.showModal = !this.showModal;
      //     // }
      //   });
      // this.getRoutes()
      // this.getUsers()

    },
    methods: {
      selectedRowItem(data){
        this.selectedItem = data.route
       let temp = JSON.parse(data.address)
        this.addressData = JSON.parse(temp)
         //console.log(JSON.parse(this.addressData))
      },
      openAssigner(data){
        this.selectedRoute = data
        this.selectedRoute.address = JSON.stringify(this.selectedRoute.address)
        this.dialogEditItem = true
      },
      async getUsers(){
           let headers = {
            'Content-Type': 'application/json',
            'Authorization': 'Bearer ' + this.$session.get('user').token,

           }
          Axios.get(this.nodeApi+'/getAllUsers',{headers:headers})
          .subscribe(res =>{
            this.users = res.data

          },err => console.log(err))
      },
     async assign(){
            this.getRoutes()
            this.dialogEditItem = false
      //  this.selectedRoute.createdBy = this.$session.get('user').userdetails.id
      //  this.selectedRoute.assignTo = this.selectedUser.id
      //  let id = this.selectedRoute.id
      //   delete  this.selectedRoute.createdAt
      //   delete  this.selectedRoute.updatedAt
      //   delete  this.selectedRoute.id
      //   delete  this.selectedRoute.userName
      //      let dataobj = {

      //          stack:this.selectedRoute,
      //          stack_id:id,
      //          assignTo:this.selectedRoute.assignTo,
      //      }
      //    //  console.log(dataobj)
      //       this.dialogloader = true

      //       let payload = this.$jwt.sign(dataobj, 'meterlink@@11',{expiresIn : 15});
      //       let data = {
      //       token: this.$session.get('user').token,
      //       payload: payload
      //       }


      //   await Axios.post(this.nodeApi+'/assignRoute',data)
      //     .subscribe(res =>{

      //       if(res.data.affectedRows){
      //       this.dialogloader = false
      //       this.getRoutes()
      //       this.dialogEditItem = false
      //         this.$swal.fire({
      //           type: 'success',
      //           title: 'Route Assignment',
      //           text:  'Route Assignment was successfully'
      //         })
      //       }
      //       if(!res.data.affectedRows) {
      //         this.dialogloader = false
      //         this.$swal.fire({
      //           type: 'error',
      //           title: 'Route Assignment Failed',
      //           text:  res.data.message
      //         })
      //       }


      //     },err => console.log(err))



      },
       async getRoutes(){
           let headers = {
            'Content-Type': 'application/json',
            'Authorization': 'Bearer ' + this.$session.get('user').token,

           }
           var token = this.$jwt.sign({ assignTo: this.selectedUser.id }, 'meterlink@@11',{expiresIn : 15});
           let obj = {
                payload: token
            }
          Axios.post(this.nodeApi+'/getAllRoutesByUser',obj,{headers:headers})
          .subscribe(res =>{
            var decoded = this.$jwt.decode(res.data.token, {complete: true});
            this.routes = decoded.payload.rows
            //console.log(this.routes)

          },err => console.log(err))



      },
      loadRoutes(){
        this.getRoutes()
      },

      updateItem(){

          if (this.editedIndex > -1) {
          Object.assign(this.itemData[this.editedIndex], this.newItem)
           this.dialogEditItem = false

          } else {
          this.itemData.push({quantity:this.newItem.quantity,item: this.newItem.item,unitprice:this.newItem.unitprice})
          this.dialogEditItem = false

          }


      },
      editItem(item){
        this.editedIndex = this.itemData.indexOf(item)
        this.newItem = Object.assign({}, item)
        this.dialogEditItem = true
      },
      deleteItem(item){
         const index = this.routes.indexOf(item)
         this.routes.splice(index, 1)
      },




    },
  }
</script>
<style scoped>
  .block {
     height: 50%;
  }
  .container {

    padding-left: 15%;
    padding-right: 15%;
  }
  .inputfield {
    width: 70%;
    height: 50px;
    text-align: end;
    font-size: 20px;
  }
   .inputfield2 {
    width: 70%;
    height: 50px;
    text-align: end;
    font-size: 15px;
  }
  .my-custom-scrollbar {
    position: relative;
    height: 780px;
    width: auto;
    overflow-y: scroll;
    overflow-x: hidden;
    }
    .scroll-y {
    display: block;
    }



</style>
