<template>
  <v-app id="inspire" class="main">
    <v-content>
      <v-container
        class="fill-height"
        fluid
        justify-center  
      >
     <!-- <img src="/images/logo.png" alt="logo" class="img1 bounceIn"> -->
        <v-row
          align="center"
          justify="center"
        >
          <v-col
            cols="12"
            sm="8"
            md="4"
          >
           
            <v-card class="elevation-12">
              <v-toolbar
                color="#2B5563"
                dark
                flat
              >
              

              <v-avatar color="teal" size="48" class="mr-5">
                <img src="/images/logo.png" alt="logo" class="bounceIn">
              </v-avatar>
                <v-toolbar-title>Meter Link Admin System</v-toolbar-title>
                <v-spacer />
               
                
              </v-toolbar>
              <v-card-text>
                <v-form>
                  <v-text-field
                    v-model="userName"
                    label="Login"
                    name="login"
                    prepend-icon="person"
                    type="text"
                    @keydown.enter="login"
                  />

                  <v-text-field
                    v-model="passWord"
                    id="password"
                    label="Password"
                    name="password"
                    prepend-icon="lock"
                    type="password"
                    @keydown.enter="login"
                  />
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-spacer />
                <v-btn large dark color="#2B5563" @click="login">Login</v-btn>
              </v-card-actions>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-content>

     <v-dialog v-model="dialogloader" hide-overlay persistent width="300">
            <v-card color="primary" dark>
                <v-card-text>
                    <br>
                   <h3>Please stand by</h3> 
                    <br>
                    <v-progress-linear indeterminate color="white" class="mb-0"></v-progress-linear>
                    <br>
                </v-card-text>
            </v-card>
    </v-dialog>
  </v-app>
</template>

<script>
import Axios from 'axios-observable'
  export default {
    name: 'Login',
    props: {
      source: String,
      
    },
    data() {
      return {
       // nodeApi: 'http://localhost:8082',
       nodeApi: '',
        dialogloader: false,
        userName:'',
        passWord:''
      }
    },
    methods: {
     
      async login(){
        // if(this.coordinatesData){
          this.dialogloader = true
          let newpassword = this.$jwt.sign(this.passWord, 'meterlink@@11');
          console.log(newpassword)
          var token = this.$jwt.sign({ userName: this.userName,password:newpassword }, 'meterlink@@11',{expiresIn : 15});

          let logindetails = {
                payload: token                
            }

          await Axios.post(this.nodeApi + '/loginUser', logindetails)
                    .subscribe(res => {
                        //console.log(res)
                        if (res.data.token) {
                          this.dialogloader = false
                          var decoded = this.$jwt.decode(res.data.token, {complete: true});
                           //console.log(decoded);
                          if(decoded.payload.rows.length > 0){
                           //console.log(decoded.payload.rows[0]);
                           let userData = {
                             token: res.data.token,
                             userdetails: decoded.payload.rows[0]
                           }
                            this.$session.start()
                              this.$session.set('user', userData)
                              this.$router.push({
                                      name: "Dashboard"
                              });
                             
                          }else{
                             this.dialogloader = false
                            this.$swal.fire({
                                type: 'error',
                                title: 'Login Failure',
                                text: 'Sorry wrong username or password, please try again.'
                            })
                          }                              
                             
                            
                        } else {
                            this.dialogloader = false
                            this.$swal.fire({
                                type: 'error',
                                title: 'Login Failure',
                                text: 'API Authorization failure : '+res.data.message
                            })
                        }
                    }, err => {
                        this.dialogloader = false
                        this.$swal.fire({
                            type: 'error',
                            title: 'Login failed please try again',
                            text: err
                        })
                    })
    
        }
    },
  }
</script>
<style scoped>
  .main {
        background-image: url('/images/possystem.jpg');        
        background-repeat: no-repeat;    
        
        
    }
     .img1 {
        display: block;
        margin: 0 auto;
        width: 150px;
        height: auto;
        top: 200px;
        position:absolute;
        top:155px;

        
    }
</style>