<template>

  <v-app id="sandbox">
    <v-app-bar color="#2B5563" dark app>

      <v-avatar color="teal" size="48" class="mr-5">
        <img src="/images/logo.png" alt="logo" class="bounceIn">
      </v-avatar>
      <v-toolbar-title>Meter Link</v-toolbar-title>
      <v-layout row pb-2 class="hidden-sm-and-down">
        <v-flex class="bounceIn" md1>
          <h1 style="margin-top:25px;">{{fullname}}</h1>
        </v-flex>
        <v-flex>

          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
                  <img
                      src="/images/routes.png"
                      alt="logo"
                      class="img1 bounceIn"
                      v-on="on"
                      @click="menu('home','Route Assignment')"

                    >
                </template>
            <span>Route Assignment</span>
          </v-tooltip>
          <!--
                 <v-tooltip bottom>
                <template v-slot:activator="{ on }">
                  <img
                      src="/images/manager.png"
                      alt="logo"
                      class="img1 bounceIn"
                      v-on="on"
                      @click="menu('managment','Route Managment')"

                    >
                </template>
                <span>Route Managment</span>
              </v-tooltip> -->

          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
                  <img
                      src="/images/map.png"
                      alt="logo"
                      class="img1 bounceIn"
                      v-on="on"
                      @click="menu('incident','Incident Report')"

                    >
                </template>
            <span>Incident Report</span>
          </v-tooltip>

          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
                  <img
                      src="/images/meter.png"
                      alt="logo"
                      class="img1 bounceIn"
                      v-on="on"
                      @click="menu('reading','Meter Readings')"

                    >
                </template>
            <span>Meter Readings</span>
          </v-tooltip>

          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
                  <img
                      src="/images/install.png"
                      alt="logo"
                      class="img1 bounceIn"
                      v-on="on"
                      @click="menu('installations','Installations')"

                    >
                </template>
            <span>Installations</span>
          </v-tooltip>

          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
                  <img
                      src="/images/settings.png"
                      alt="logo"
                      class="img1 bounceIn"
                      v-on="on"
                      @click="menu('settings','Settings')"

                    >
                </template>
            <span>Settings</span>
          </v-tooltip>

        </v-flex>

        <v-spacer></v-spacer>

        <h2 class="bounceIn menuSelected">{{meunSelected}}</h2>

        <v-tooltip bottom>
          <template v-slot:activator="{ on }">
                    <img
                      src="/images/exit.png"
                      alt="logo"
                      class="img3 bounceIn"
                      v-on="on"
                      @click="logout"

                    >
                  </template>
          <span>Log Out</span>
        </v-tooltip>

      </v-layout>

    </v-app-bar>

    <component v-bind:is="page"></component>

  </v-app>

</template>

<script>

  import home from './Home'
  import settings from './Settings'
  import managment from './Managment'
  import maps from './Map'
  import reading from './Readings'
  import installations from './Installations'
  import incident from './Incident'

  export default {
    name: 'Dashboard',
    components: {
      home,
      settings,
      managment,
      maps,
      reading,
      installations,
      incident
    },
    data: () => ({
      fullname: '',
      meunSelected: 'Route Assignment',
      page: ''
    }),
    mounted() {
      this.page = 'home'
    },
    methods: {
      logout() {
        this.$router.push({ name: 'Login' })
        this.$session.destroy()
      },
      menu(data, menuData) {
        this.meunSelected = menuData
        this.page = ''
        this.page = data
      }
    }
  }

</script>

<style scoped>

  .img1 {
    margin-top: 15px;
    margin-right: 25px;
    width: 50px;
    height: auto;
  }

  .img1:hover {
    cursor: pointer;
    opacity: 0.5;
    -webkit-transform: scale3d(0.95, 0.95, 1);
    transform: scale3d(0.95, 0.95, 1);
  }

  .img3 {
    margin-top: 15px;
    margin-right: 25px;
    width: 50px;
    height: 50px;
  }
  .img3:hover {
    cursor: pointer;
    opacity: 0.5;
    -webkit-transform: scale3d(0.95, 0.95, 1);
    transform: scale3d(0.95, 0.95, 1);
  }
  .menuSelected {
    margin-top: 20px;
    margin-right: 25px;
  }

</style>
