<template>
<div class="adminmap">
    <l-map :zoom="zoom" :center="center">
        <l-tile-layer :url="url" :attribution="attribution"></l-tile-layer>
        <l-marker v-for="(branch,index) in address" :key="index" :lat-lng="latlong(branch.lat,branch.log)">
            <l-popup :content="`Customer : <b>${branch.clientName}</b><br>Address &nbsp;&nbsp;: <b>${branch.address} </b><br>Date &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;: <b>${new Date(branch.dateTime).toLocaleString('en-GB', { timeZone: 'UTC' })}</b>`" />
            <l-icon :icon-size="iconSize" :icon-url="icon">
            </l-icon>

        </l-marker>
    </l-map>
</div>
</template>

<script>
/* eslint no-undef: 0 */
import {
    LMap,
    LTileLayer,
    LMarker,
    LIcon,
    LPopup
} from 'vue2-leaflet';

import africabet from '@/components/logo.png';
//
// import Popper from 'vue-popperjs';
// import 'vue-popperjs/dist/vue-popper.css';
export default {
    components: {
        LMap,
        LTileLayer,
        LMarker,
        LIcon,
        LPopup,
        //'popper': Popper
    },
    props: ['mapData'],
    data() {
        return {
            zoom: 8,
            center: L.latLng(-17.79548160, 31.02228480),
            url: 'https://tile.thunderforest.com/neighbourhood/{z}/{x}/{y}.png?apikey=130626f3c0ed4ca5865efcbbbe4334f8',
            attribution: '&copy; <a href="http://grebles.co.zw/copyright">Grebles Solutions</a>',
            marker: L.latLng(-17.79548160, 31.02228480),
            icon: africabet,
            iconSize: [32, 32],
            address: []
        }
    },
    created() {
        //do something after creating vue instance
        this.address = this.mapData
        //console.log(this.address)
    },
    watch: {
        mapData: {
            handler(val) {
                if (val) {
                    this.address = this.mapData
                    console.log(this.address)
                }

            },
            deep: true
        }
    },
    methods: {
        latlong(lat, lng) {
            return L.latLng(lat, lng)
        }
    },
}
</script>

<style scoped>
.adminmap {
    width: 100%;
    height: 100%;
    border-left: 5px solid #6fbe8d;
    border-top: 5px solid #6fbe8d;
    border-bottom: 5px solid #6fbe8d;
    background-color: rgb(255, 255, 255);
}
</style>
