<template>
<v-flex ma-5>
    <v-stepper v-model="e1">
        <v-stepper-header>
            <v-stepper-step :editable="editable" step="1">User Administration</v-stepper-step>

            <v-divider></v-divider>

            <v-stepper-step :editable="editable" step="2">No Access Administration</v-stepper-step>

        </v-stepper-header>

        <v-stepper-items>
            <v-stepper-content step="1">
                <v-data-table :headers="headersUsers" :items="users" sort-by="calories" class="elevation-1">
                    <template v-slot:top>
                        <v-toolbar flat color="white">
                            <v-toolbar-title>Users List</v-toolbar-title>
                            <v-divider class="mx-4" inset vertical></v-divider>
                            <v-spacer></v-spacer>

                            <v-dialog v-model="dialogUsers" width="500">
                                <template v-slot:activator="{ on }">
                                    <v-btn color="primary" dark class="mb-2" v-on="on">New User</v-btn>
                                </template>
                                <v-card>
                                    <v-card-title class="headline grey lighten-2" primary-title>
                                        <span class="headline">{{ formTitle }}</span>
                                    </v-card-title>
                                    <v-flex pa-3>
                                        <v-combobox v-model="editedUser.userType" :items="userTypeData" item-text="name" hide-selected label="Select user type"></v-combobox>

                                        <v-text-field label="Fist Name" outlined type="text" v-model="editedUser.firstName"></v-text-field>
                                        <v-text-field label="Last Name" outlined type="text" v-model="editedUser.lastName"></v-text-field>
                                        <v-text-field label="UserName" outlined type="text" v-model="editedUser.userName"></v-text-field>
                                        <v-text-field v-if="editedIndex === -1" label="Password" outlined type="password" v-model="editedUser.password"></v-text-field>
                                        <v-text-field v-if="editedIndex === -1" label="Confirm Password" outlined type="password" v-model="confirm_password"></v-text-field>

                                        <v-btn v-if="editedIndex === -1" @click="save" large block color="primary" class="mb-5">Save</v-btn>
                                        <v-btn v-if="editedIndex !== -1" @click="update" large block dark color="green" class="mb-5">Update</v-btn>
                                        <v-btn @click="close" large block dark color="red">Cancel</v-btn>

                                    </v-flex>
                                    <v-card-text>

                                    </v-card-text>
                                </v-card>
                            </v-dialog>

                        </v-toolbar>
                    </template>
                    <template v-slot:item.action="{ item }">

                        <v-layout row>

                            <v-tooltip top>
                                <template v-slot:activator="{ on, attrs }">
                                    <v-icon color="green" class="mr-3" @click="editUser(item)" v-bind="attrs" v-on="on">
                                        edit
                                    </v-icon>
                                </template>
                                <span>Edit User</span>
                            </v-tooltip>
                            <!--
                            <v-tooltip top>
                                <template v-slot:activator="{ on, attrs }">
                                    <v-icon v-if="item.status == 1" color="green" class="mr-3" @click="userActivation(2,item.id)" v-bind="attrs" v-on="on">
                                        account_circle
                                    </v-icon>
                                </template>
                                <span>De-Activate User</span>
                            </v-tooltip>
                            <v-tooltip top>
                                <template v-slot:activator="{ on, attrs }">
                                    <v-icon v-if="item.status == 2" color="grey" class="mr-3" @click="userActivation(1,item.id)" v-bind="attrs" v-on="on">
                                        account_circle
                                    </v-icon>
                                </template>
                                <span>Activate User</span>
                            </v-tooltip>
                            -->
                            <v-tooltip top>
                                <template v-slot:activator="{ on, attrs }">
                                    <v-icon color="red" class="mr-3" @click="passwordReset(1,item.id)" v-bind="attrs" v-on="on">
                                        vpn_key
                                    </v-icon>
                                </template>
                                <span>Reset Password</span>
                            </v-tooltip>
                            <v-tooltip top>
                                <template v-slot:activator="{ on, attrs }">
                                    <v-icon color="orange" class="mr-3" @click="appPasswordReset(1,item.id)" v-bind="attrs" v-on="on">
                                        vpn_key
                                    </v-icon>
                                </template>
                                <span>Reset App Password</span>
                            </v-tooltip>
                        </v-layout>

                    </template>
                    <template v-slot:no-data>
                        <v-btn color="primary" @click="initialize">Reset</v-btn>
                    </template>
                </v-data-table>

            </v-stepper-content>

            <v-stepper-content step="2">
                <v-data-table :headers="headersAccess" :items="noAccessData" sort-by="calories" class="elevation-1">
                    <template v-slot:top>
                        <v-toolbar flat color="white">
                            <v-toolbar-title>No Access List</v-toolbar-title>
                            <v-divider class="mx-4" inset vertical></v-divider>
                            <v-spacer></v-spacer>

                            <v-dialog v-model="dialogNoAccess" width="500">
                                <template v-slot:activator="{ on }">
                                    <v-btn color="primary" dark class="mb-2" v-on="on">New No Access</v-btn>
                                </template>
                                <v-card>
                                    <v-card-title class="headline grey lighten-2" primary-title>
                                        <span class="headline">{{ formTitle2 }}</span>
                                    </v-card-title>
                                    <v-flex pa-3>

                                        <v-text-field label="No Access Name" outlined type="text" v-model="noAccess.description"></v-text-field>

                                        <v-btn v-if="editedIndex2 === -1" @click="saveNoAccess" large block color="primary" class="mb-5">Save</v-btn>
                                        <v-btn v-if="editedIndex2 !== -1" @click="updateNoAccess" large block dark color="green" class="mb-5">Update</v-btn>
                                        <v-btn @click="close" large block dark color="red">Cancel</v-btn>

                                    </v-flex>

                                </v-card>
                            </v-dialog>

                        </v-toolbar>
                    </template>
                    <template v-slot:item.action="{ item }">

                        <v-layout row>
                            <v-icon color="blue" class="mr-5" @click="editUser2(item)">
                                edit
                            </v-icon>

                        </v-layout>

                    </template>
                    <template v-slot:no-data>
                        <v-btn color="primary" @click="initialize">Reset</v-btn>
                    </template>
                </v-data-table>

            </v-stepper-content>

        </v-stepper-items>
    </v-stepper>
    <v-dialog v-model="dialogloader" hide-overlay persistent width="300">
        <v-card color="primary" dark>
            <v-card-text>
                <br>
                <h3>Please stand by</h3>
                <br>
                <v-progress-linear indeterminate color="white" class="mb-0"></v-progress-linear>
                <br>
            </v-card-text>
        </v-card>
    </v-dialog>
</v-flex>
</template>

<script>
import Axios from 'axios-observable'
export default {
    data: () => ({
        dialog: false,
        dialogloader: false,
        status: false,
        truer: true,

        noAccessData: [],
        falser: false,
        e1: 0,
        is_company: false,
        apiURL: 'http://localhost:81/clearplus/api',
        nodeApi: '',
        editable: true,
        dialogUsers: false,
        dialogNoAccess: false,
        headersAccess: [{
                text: 'Code ID',
                align: 'left',
                sortable: false,
                value: 'id'
            },
            {
                text: 'Description',
                value: 'description'
            },

            {
                text: 'Actions',
                value: 'action',
                sortable: false
            }
        ],
        headersUsers: [{
                text: 'User ID',
                align: 'left',
                sortable: false,
                value: 'id'
            },
            {
                text: 'User Name',
                align: 'left',
                sortable: false,
                value: 'userName'
            },
            {
                text: 'firstName',
                value: 'firstName'
            },
            {
                text: 'lastName',
                value: 'lastName'
            },
            {
                text: 'User Type',
                value: 'userType'
            },
            {
                text: 'Actions',
                value: 'action',
                sortable: false
            }
        ],

        users: [],
        clients: [],
        editedIndex: -1,
        editedIndex2: -1,
        noAccess: {
            description: ''
        },
        defaultnoAccess: {
            description: ''
        },
        editedUser: {
            userName: '',
            firstName: '',
            lastName: '',
            userType: '',
            status: '',
            password: '',

        },
        defaultUser: {
            userName: '',
            firstName: '',
            lastName: '',
            userType: '',
            status: '',
            password: '',

        },
        confirm_password: '',
        userTypeData: []
    }),

    computed: {
        formTitle() {
            return this.editedIndex === -1 ? 'New User' : 'Edit User'
        },
        formTitle2() {
            return this.editedIndex2 === -1 ? 'New No Access' : 'Edit No Access'
        }
    },

    watch: {
        dialogUsers(val) {
            val || this.close()
        },
        dialogNoAccess(val) {
            val || this.close()
        }
    },

    created() {
        this.initialize()
    },

    methods: {
        async initialize() {
            await this.getUserTypes()
            await this.getUsers()
            await this.getNoAccess()
        },
        getStatus(data) {
            // console.log(data)
            if (data == true || data == 'true') {
                // this.status = true
                return 'Active'
            } else if (data == false || data == 'false') {
                //this.status = false
                return 'In-Active'
            }
        },

        async getUserTypes() {
            let headers = {
                'Content-Type': 'application/json',
                Authorization: 'Bearer ' + this.$session.get('user').token
            }
            Axios.get(this.nodeApi + '/getAllUserTypes', {
                headers: headers
            }).subscribe(
                res => {
                    this.userTypeData = res.data
                    //console.log(res.data.recordset)
                },
                err => console.log(err)
            )
        },
        async getUsers() {
            let headers = {
                'Content-Type': 'application/json',
                Authorization: 'Bearer ' + this.$session.get('user').token
            }
            Axios.get(this.nodeApi + '/getAllUsers', {
                headers: headers
            }).subscribe(
                res => {
                    this.users = res.data
                },
                err => console.log(err)
            )
        },
        async getNoAccess() {
            let headers = {
                'Content-Type': 'application/json',
                Authorization: 'Bearer ' + this.$session.get('user').token
            }
            Axios.get(this.nodeApi + '/getAllNoAccess', {
                headers: headers
            }).subscribe(
                res => {
                    this.noAccessData = res.data
                },
                err => console.log(err)
            )
        },

        editUser(item) {
            this.editedIndex = this.users.indexOf(item)
            this.editedUser = Object.assign({}, item)
            this.dialogUsers = true
        },
        editUser2(item) {
            this.editedIndex2 = this.noAccessData.indexOf(item)
            this.noAccess = Object.assign({}, item)
            this.dialogNoAccess = true
        },

        deleteUser(item) {
            const index = this.users.indexOf(item)
            confirm('Are you sure you want to delete this item?') && this.users.splice(index, 1)
        },

        close() {
            this.dialogUsers = false
            this.dialogNoAccess = false
            this.dialogloader = false
            setTimeout(() => {
                this.editedUser = Object.assign({}, this.defaultItem)
                this.noAccess = Object.assign({}, this.defaultnoAccess)
                this.editedIndex = -1
                this.editedIndex2 = -1
            }, 300)
        },

        async save() {
            if (!this.editedUser.userType) {
                this.$swal.fire('User type Error', 'Please select a user type.', 'error')
            } else if (!this.editedUser.firstName || !this.editedUser.lastName) {
                this.$swal.fire('Name Error', 'Please enter Firt Name and Last Name.', 'error')
            } else if (!this.editedUser.userName) {
                this.$swal.fire('User Name Error', 'Please enter vaild username.', 'error')
            } else if (!this.editedUser.password) {
                this.$swal.fire('Password Error', 'Please enter vaild password.', 'error')
            } else if (this.editedUser.password != this.confirm_password) {
                this.$swal.fire('Password Error', 'Your 2 passwords do not match.', 'error')
            } else {
                let userType = this.editedUser.userType.id
                this.dialogloader = true
                // this.editedUser.password = 'eyJhbGciOiJIUzI1NiJ9.MTIzNA.2033UUIOKN4Jh_nHjbb1-_ppomxZ0-2gMxSdzqw3vZ8'
                let new_password = this.$jwt.sign(this.editedUser.password, 'meterlink@@11')

                this.editedUser.password = new_password

                this.editedUser.passwordApp =
                    'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJwYXlsb2FkIjoiMDAwMCJ9.ah-ibXJDwm8q6d2WR0ScwJB5ThnnpuWhCGOQF_YoFJg'
                this.editedUser.status = 2
                this.editedUser.userType = userType
                this.editedUser.createdBy = this.$session.get('user').userdetails.id

                // console.log(this.editedUser)
                let payload = this.$jwt.sign(this.editedUser, 'meterlink@@11', {
                    expiresIn: 15
                })

                let data = {
                    token: this.$session.get('user').token,
                    payload: payload
                }

                Axios.post(this.nodeApi + '/addUser', data).subscribe(
                    res => {
                        if (res.data.affectedRows) {
                            this.close()
                            this.editedUser = this.defaultUser
                            this.getUsers()
                            this.$swal.fire(

                                'User Saved',
                                'User was saved successfully',
                                'success'
                            )
                        }
                        if (!res.data.affectedRows) {
                            this.dialogloader = false
                            this.$swal.fire(

                                'User Saving Failed',
                                `${res.data.message}`,
                                'error',
                            )
                        }
                    },
                    err => console.log(err)
                )
            }
        },

        async update() {
            if (!this.editedUser.userType) {
                this.$swal.fire('User type Error', 'Please select a user type.', 'error')
            } else if (!this.editedUser.firstName || !this.editedUser.lastName) {
                this.$swal.fire('Name Error', 'Please enter Firt Name and Last Name.', 'error')
            } else if (!this.editedUser.userName) {
                this.$swal.fire('Name Error', 'Please enter vaild username.', 'error')
            } else {
                this.dialogloader = true
                let id = this.editedUser.id

                if (this.editedUser.userType.id) {
                    let userType = this.editedUser.userType.id
                    this.editedUser.userType = userType
                } else {
                    if (this.editedUser.userType == 'Admin') {
                        this.editedUser.userType = 1
                    }
                    if (this.editedUser.userType == 'Meter Reader') {
                        this.editedUser.userType = 2
                    }
                    if (this.editedUser.userType == 'Plumber') {
                        this.editedUser.userType = 3
                    }
                }
                if (this.editedUser.status == 'active') {
                    this.editedUser.status = 1
                }
                if (this.editedUser.status == 'in-active') {
                    this.editedUser.status = 2
                }
                delete this.editedUser.id
                delete this.editedUser.iat
                delete this.editedUser.exp
                // console.log(this.editedUser)
                let payload = this.$jwt.sign(this.editedUser, 'meterlink@@11', {
                    expiresIn: 15
                })

                let data = {
                    token: this.$session.get('user').token,
                    payload: payload
                }

                Axios.put(this.nodeApi + '/updateUser/' + id, data).subscribe(
                    res => {
                        if (res.data.affectedRows) {
                            this.close()
                            this.editedUser = this.defaultUser
                            this.getUsers()
                            this.$swal.fire(

                                'User Update',
                                'User was updated successfully',
                                'success',
                            )
                        }
                        if (!res.data.affectedRows) {
                            this.$swal.fire(

                                'User Update Failed',
                                `${res.data.message}`,
                                'error',
                            )
                        }
                    },
                    err => console.log(err)
                )
            }
        },

        async saveNoAccess() {
            if (!this.noAccess.description) {
                this.$swal.fire('No Access Error', 'Please enter a no access description.', 'error')
            } else {
                this.dialogloader = true
                let payload = this.$jwt.sign(this.noAccess, 'meterlink@@11', {
                    expiresIn: 15
                })

                let data = {
                    token: this.$session.get('user').token,
                    payload: payload
                }

                Axios.post(this.nodeApi + '/addNoAccess', data).subscribe(
                    res => {
                        if (res.data.affectedRows) {
                            this.close()
                            this.editedUser = this.defaultUser
                            this.getNoAccess()
                            this.$swal.fire(
                                'NoAccess Added',
                                'NoAccess was added successfully',
                                'success',
                            )
                        }
                        if (!res.data.affectedRows) {
                            this.dialogloader = false
                            this.$swal.fire(

                                'User Saving Failed',
                                `${res.data.message}`,
                                'error',
                            )
                        }
                    },
                    err => console.log(err)
                )
            }
        },

        async updateNoAccess() {
            if (!this.noAccess.description) {
                this.$swal.fire('No Access Error', 'Please enter a no access description.', 'error')
            } else {
                this.dialogloader = true
                let id = this.noAccess.id

                delete this.noAccess.id
                delete this.noAccess.iat
                delete this.noAccess.exp
                // console.log(this.noAccess)
                let payload = this.$jwt.sign(this.noAccess, 'meterlink@@11', {
                    expiresIn: 15
                })

                let data = {
                    token: this.$session.get('user').token,
                    payload: payload
                }

                Axios.put(this.nodeApi + '/updateNoAccess/' + id, data).subscribe(
                    res => {
                        if (res.data.affectedRows) {
                            this.close()
                            this.noAccess = this.defaultnoAccess
                            this.getNoAccess()
                            this.$swal.fire(
                                'NoAccess Updated',
                                'NoAccess was updated successfully',
                                'success',
                            )
                        }
                        if (!res.data.affectedRows) {
                            this.$swal.fire(

                                'User Saving Failed',
                                `${res.data.message}`,
                                'error',
                            )
                        }
                    },
                    err => console.log(err)
                )
            }
        },

        async userActivation(dataStatus, id) {
            let obj = {
                status: dataStatus
            }

            //console.log(this.editedUser)
            let payload = this.$jwt.sign(obj, 'meterlink@@11', {
                expiresIn: 15
            })

            let data = {
                token: this.$session.get('user').token,
                payload: payload
            }

            Axios.put(this.nodeApi + '/updateUser/' + id, data).subscribe(
                res => {
                    if (res.data.affectedRows) {
                        this.initialize()
                        console.log('Success')
                    }
                    if (!res.data.affectedRows) {
                        console.log(res.data.message)
                    }
                },
                err => console.log(err)
            )
        },

        async passwordReset(dataStatus, id) {
            let obj = {
                password: 'eyJhbGciOiJIUzI1NiJ9.MTIzNA.2033UUIOKN4Jh_nHjbb1-_ppomxZ0-2gMxSdzqw3vZ8'
            }

            //console.log(this.editedUser)
            let payload = this.$jwt.sign(obj, 'meterlink@@11', {
                expiresIn: 15
            })

            let data = {
                token: this.$session.get('user').token,
                payload: payload
            }

            Axios.put(this.nodeApi + '/adminPasswordReset/' + id, data).subscribe(
                res => {
                    if (res.data.affectedRows) {
                        this.initialize()
                        this.$swal.fire(
                            'User Admin Password Update',
                            'User was updated successfully and has been set to the default "1234"',
                            'success',
                        )
                    }
                    if (!res.data.affectedRows) {
                        console.log(res.data.message)
                    }
                },
                err => console.log(err)
            )
        },

        async appPasswordReset(dataStatus, id) {
            let obj = {
                passwordApp: 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJwYXlsb2FkIjoiMDAwMCJ9.ah-ibXJDwm8q6d2WR0ScwJB5ThnnpuWhCGOQF_YoFJg'
            }

            //console.log(this.editedUser)
            let payload = this.$jwt.sign(obj, 'meterlink@@11', {
                expiresIn: 15
            })

            let data = {
                token: this.$session.get('user').token,
                payload: payload
            }

            Axios.put(this.nodeApi + '/adminPasswordResetApp/' + id, data).subscribe(
                res => {
                    if (res.data.affectedRows) {
                        this.initialize()
                        this.$swal.fire(
                            'User App Password Update',
                            'User was updated successfully and has been set to the default "0000"',
                            'success',
                        )
                    }
                    if (!res.data.affectedRows) {
                        console.log(res.data.message)
                    }
                },
                err => console.log(err)
            )
        }
    }
}
</script>

<style scoped>
.container {
    max-width: 1000;
    padding-left: 15%;
    padding-right: 15%;
}
</style>
