<template>
<v-flex ma-5>

    <v-row>
        <v-col cols="12" md="5">

            <v-card class="mb-5" max-height="850">
                <v-card-title class="headline blue-grey lighten-1 white--text" primary-title>
                    Assigned Routes
                    <v-spacer></v-spacer>
                    <v-btn color="green" dark @click="dialogEditItem = true">Select User</v-btn>
                </v-card-title>
                <v-simple-table fixed-header height="100%">
                    <template v-slot:default>
                        <thead style="overflow-y: scroll;overflow-x: hidden;">
                            <tr>
                                <th class="text-left">ID</th>
                                <th class="text-left">Name</th>
                                <th class="text-left">Status</th>
                                <th class="text-left">Action</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="item in routes" :key="item.routeId">

                                <td @click="selectedRowItem(item)">
                                    {{ item.routeId }}
                                </td>
                                <td @click="selectedRowItem(item)">{{ item.route }}</td>
                                <td @click="selectedRowItem(item)">{{ item.name }}</td>
                                <td>

                                    <!-- <v-btn v-if="item.name != 'uploaded'" icon @click="deleteItem(item)">
                                        <v-icon color="red">delete</v-icon>
                                    </v-btn> -->

                                    <v-btn v-if="item.name == 'completed'" icon @click="mapScreen(item)">
                                        <v-icon color="green">location_on</v-icon>
                                    </v-btn>

                                    <v-tooltip v-if="item.name == 'completed'" top>
                                        <template v-slot:activator="{ on }">
                                            <v-btn icon>
                                                <export-excel class="btn btn-default" :data="addressData" :fields="json_fields" :title="json_title" worksheet="Route_Worksheet" :name="'FinishedRoutes_'+item.routeId+'.xls'">

                                                    <v-icon v-on="on" color="green darken-2">library_add</v-icon>

                                                </export-excel>
                                            </v-btn>
                                        </template>
                                        <span>Billing Export</span>
                                    </v-tooltip>

                                     <v-tooltip v-if="item.name == 'completed'" top>
                                        <template v-slot:activator="{ on }">
                                            <v-btn icon>
                                                <export-excel class="btn btn-default" :data="addressData" :fields="json_fields2" :title="json_title" worksheet="Route_Worksheet" :name="'GIS_'+item.routeId+'.xls'">

                                                    <v-icon v-on="on" color="red darken-2">library_add</v-icon>

                                                </export-excel>
                                            </v-btn>
                                        </template>
                                        <span>GIS Export</span>
                                    </v-tooltip>

                                     <v-tooltip v-if="item.name == 'completed'" top>
                                        <template v-slot:activator="{ on }">
                                            <v-btn icon>
                                                <export-excel class="btn btn-default" :data="addressData" :fields="json_fields2" :title="json_title2" worksheet="Route_Worksheet" :name="'Route_'+item.routeId+'.csv'">

                                                    <v-icon v-on="on" color="purple darken-2">library_add</v-icon>
 
                                                </export-excel>
                                            </v-btn>
                                        </template>
                                        <span>Route Export</span>
                                    </v-tooltip>

                                </td>
                            </tr>
                        </tbody>
                    </template>
                </v-simple-table>
            </v-card>

        </v-col>
        <v-col cols="12" md="7">

            <v-card hover class="mb-12" max-height="850">
                <v-card-title class="headline blue-grey lighten-1 white--text" primary-title>
                    Routes : {{selectedItem}} Address List
                    <v-spacer></v-spacer>

                </v-card-title>

                <v-simple-table fixed-header height="100%">
                    <template v-slot:default>
                        <thead style="overflow-y: scroll;overflow-x: hidden;">
                            <tr>

                                <th class="text-left">meterNumber</th>
                                <th class="text-left">Previous Reading</th>
                                <th class="text-left">Reading</th>
                                <th class="text-left">No-Access</th>
                                <th class="text-left">Notes</th>

                                <th width="20%" class="text-left">Date Time</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="item in addressData" :key="item.item">

                                <td>{{ item.meterNumber }}</td>
                                <td>{{ item.lastReading }}</td>
                                <td>{{ item.reading }}</td>
                                <td>{{ item.noAccess }}</td>
                                <td>{{ item.notes }}</td>

                                <td>{{ dateChecker(item.dateTime) }}</td>
                            </tr>
                        </tbody>
                    </template>
                </v-simple-table>
            </v-card>

        </v-col>

    </v-row>

    <v-dialog v-model="dialogEditItem" width="500">

        <v-card>
            <v-card-title class="headline grey lighten-2" primary-title>
                User Selection

            </v-card-title>
            <v-flex ma-3>
                <v-combobox v-model="selectedUser" :items="users" item-text="userName" hide-selected label="Select user type"></v-combobox>

                <v-btn @click="assign" large block color="primary">Assign User to route</v-btn>
            </v-flex>
            <v-card-text>

            </v-card-text>

        </v-card>
    </v-dialog>

    <v-dialog v-model="dialogMap" fullscreen hide-overlay transition="dialog-bottom-transition">

        <v-card>
            <v-toolbar dark color="#2B5563">
                <v-toolbar-title>Addresses on routes</v-toolbar-title>
                <v-spacer></v-spacer>
                <v-toolbar-items>
                    <v-btn dark text @click="dialogMap = false">Close</v-btn>
                </v-toolbar-items>
            </v-toolbar>

            <v-flex xs12 lg12>
                <div class="map">
                    <MapScreen :mapData="geoData" />
                </div>
            </v-flex>

        </v-card>
    </v-dialog>

    <v-dialog v-model="dialogloader" hide-overlay persistent width="300">
        <v-card color="primary" dark>
            <v-card-text>
                <br>
                <h3>Please stand by</h3>
                <br>
                <v-progress-linear indeterminate color="white" class="mb-0"></v-progress-linear>
                <br>
            </v-card-text>
        </v-card>
    </v-dialog>
</v-flex>
</template>

<script>
import MapScreen from './Map'
import Axios from 'axios-observable'
export default {
    components: {
        MapScreen
    },
    data() {
        return {
            e1: 0,
            select: null,
            selectedUser: null,
            geoData: null,
            dialogEditItem: false,
            users: [],
            dialogMap: false,
            newrows: [],
            dialogloader: false,
            nodeApi: '',
            selectedRoute: null,
            selectedItem: null,
            routes: [],
            addressData: [],
            editedIndex: -1,
            json_title: [

            ],
            json_title2:[],
            json_fields2: {
                'lat': 'lat',
                'log': 'log',
                'icon': 'icon',
                'notes': 'notes',
                'status': 'status',
                'address': 'address',
                'reading': 'reading',
                'lastReading': 'lastReading',
                'dateTime': {
                    field: 'dateTime',
                    callback: (value) => {
                        if(value){
                        //   let newDate = new Date(value);
                        //   var dd = newDate.getDate();
                        //   var mm = newDate.getMonth()+1; 
                        //   var yyyy = newDate.getFullYear();
                        //   //return `${dd}/${mm}/${yyyy}`;
                        //   return `${new Date(item.dateTime).toLocaleString('en-GB')}`;
                        return `${new Date(value).toLocaleString('en-GB')}`;
                        
                        }else{

                          return ` `;
                        }

                    }
                },
                'noAccess': 'noAccess',
                'clientName': 'clientName',
                'meterNumber': 'meterNumber',
                'meterLocation': 'meterLocation',

            },

             json_fields: {
                'meterNumber': 'meterNumber',
                'reading': 'reading',
                'noAccess': 'noAccess'
               
            },


        }
    },
    watch: {
        dialogEditItem(val) {
            if (val) {
                this.getUsers()
            } else {
                this.editedIndex = -1
            }
        }
    },

    created() {
        //  window.addEventListener('keydown', (e) => {
        //   //  console.log(e.keyCode)
        //   //  console.log(e.key)
        //    this.inputer(e.key)
        //     // if (e.key == 'Escape') {
        //     //   this.showModal = !this.showModal;
        //     // }
        //   });
        // this.getRoutes()
        // this.getUsers()

    },
    methods: {
        dateChecker(data){
            var newdate  = new Date(data).toLocaleString('en-GB')
            if(newdate == 'Invalid Date'){
                return ''
            }else{
                return newdate
            }
        },
        mapScreen(item) {
            // console.log(item)

            let temp = JSON.parse(item.address)
            this.addressData = JSON.parse(temp)
            this.geoData = this.addressData
            this.dialogMap = true
        },
        selectedRowItem(data) {
            this.selectedItem = data.route
            let temp = JSON.parse(data.address)
            this.addressData = JSON.parse(temp)
            this.json_title = [
                'METERLINK - FINISHED ROUTE', `Route : ${this.selectedItem}`, `Route ID : ${data.routeId}`, `Downloaded Date : ${new Date(Number(data.downloadAt)).toLocaleString('en-GB')}`, `Updated Date : ${new Date(Number(data.uploadedAt)).toLocaleString('en-GB')}`, `Uploaded By : ${data.userName}`, ' '
            ]
            //console.log(data)
            //console.log(JSON.parse(this.addressData))
        },
        openAssigner(data) {
            this.selectedRoute = data
            this.selectedRoute.address = JSON.stringify(this.selectedRoute.address)
            this.dialogEditItem = true
        },
        async getUsers() {
            let headers = {
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + this.$session.get('user').token,

            }
            Axios.get(this.nodeApi + '/getAllUsers', {
                    headers: headers
                })
                .subscribe(res => {
                    this.users = res.data

                }, err => console.log(err))
        },
        async assign() {
            this.getRoutes()
            this.dialogEditItem = false
            //  this.selectedRoute.createdBy = this.$session.get('user').userdetails.id
            //  this.selectedRoute.assignTo = this.selectedUser.id
            //  let id = this.selectedRoute.id
            //   delete  this.selectedRoute.createdAt
            //   delete  this.selectedRoute.updatedAt
            //   delete  this.selectedRoute.id
            //   delete  this.selectedRoute.userName
            //      let dataobj = {

            //          stack:this.selectedRoute,               
            //          stack_id:id,
            //          assignTo:this.selectedRoute.assignTo,
            //      }
            //    //  console.log(dataobj)
            //       this.dialogloader = true

            //       let payload = this.$jwt.sign(dataobj, 'meterlink@@11',{expiresIn : 15});
            //       let data = {
            //       token: this.$session.get('user').token,
            //       payload: payload
            //       }

            //   await Axios.post(this.nodeApi+'/assignRoute',data)
            //     .subscribe(res =>{             

            //       if(res.data.affectedRows){
            //       this.dialogloader = false
            //       this.getRoutes()
            //       this.dialogEditItem = false
            //         this.$swal.fire({
            //           type: 'success',
            //           title: 'Route Assignment',
            //           text:  'Route Assignment was successfully'
            //         })
            //       }     
            //       if(!res.data.affectedRows) {
            //         this.dialogloader = false
            //         this.$swal.fire({
            //           type: 'error',
            //           title: 'Route Assignment Failed',
            //           text:  res.data.message
            //         })
            //       }

            //     },err => console.log(err))

        },
        async getRoutes() {
            let headers = {
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + this.$session.get('user').token,

            }
            var token = this.$jwt.sign({
                assignTo: this.selectedUser.id
            }, 'meterlink@@11', {
                expiresIn: 15
            });
            let obj = {
                payload: token
            }
            Axios.post(this.nodeApi + '/getAllRoutesByUser', obj, {
                    headers: headers
                })
                .subscribe(res => {
                    var decoded = this.$jwt.decode(res.data.token, {
                        complete: true
                    });
                    this.routes = decoded.payload.rows
                    //   console.log(this.routes)

                }, err => console.log(err))

        },
        loadRoutes() {
            this.getRoutes()
        },

        updateItem() {

            if (this.editedIndex > -1) {
                Object.assign(this.itemData[this.editedIndex], this.newItem)
                this.dialogEditItem = false

            } else {
                this.itemData.push({
                    quantity: this.newItem.quantity,
                    item: this.newItem.item,
                    unitprice: this.newItem.unitprice
                })
                this.dialogEditItem = false

            }

        },
        editItem(item) {
            this.editedIndex = this.itemData.indexOf(item)
            this.newItem = Object.assign({}, item)
            this.dialogEditItem = true
        },
        deleteItem(item) {
            const index = this.routes.indexOf(item)
            this.routes.splice(index, 1)
        },

    },
}
</script>

<style scoped>
.map {
    height: 90vh;
}

.block {
    height: 50%;
}

.container {

    padding-left: 15%;
    padding-right: 15%;
}

.inputfield {
    width: 70%;
    height: 50px;
    text-align: end;
    font-size: 20px;
}

.inputfield2 {
    width: 70%;
    height: 50px;
    text-align: end;
    font-size: 15px;
}

.my-custom-scrollbar {
    position: relative;
    height: 780px;
    width: auto;
    overflow-y: scroll;
    overflow-x: hidden;
}

.scroll-y {
    display: block;
}
</style>
