<template>
<v-flex ma-5>

    <v-row>
        <v-col cols="12" md="3">

            <v-card class="mb-5" max-height="850">
                <v-card-title class="headline blue-grey lighten-1 white--text" primary-title>
                    Uploaded Incidents
                    <v-spacer></v-spacer>
                    <v-btn color="green" dark @click="dialogEditItem = true">Select User</v-btn>
                </v-card-title>
                <v-simple-table fixed-header height="100%">
                    <template v-slot:default>
                        <thead style="overflow-y: scroll;overflow-x: hidden;">
                            <tr>
                                <th width="10%" class="text-left">ID</th>
                                <th width="30%" class="text-left">User</th>
                                <th width="30%" class="text-left">Status</th>
                                <th class="text-left">Action</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="item in routes" :key="item.routeId">

                                <td @click="selectedRowItem(item)">
                                    {{ item.id }}
                                </td>
                                <td @click="selectedRowItem(item)">{{ item.userName }}</td>
                                <td @click="selectedRowItem(item)">{{ item.statusName }}</td>
                                <td>
                                    <v-layout row>
                                        <v-btn icon @click="deleteItem(item)">
                                            <v-icon color="red">delete</v-icon>
                                        </v-btn>

                                    </v-layout>

                                </td>
                            </tr>
                        </tbody>
                    </template>
                </v-simple-table>
            </v-card>

        </v-col>
        <v-col cols="12" md="5">

            <v-card hover class="mb-12" max-height="850">
                <v-card-title class="headline blue-grey lighten-1 white--text" primary-title>
                    Incident List
                    <v-spacer></v-spacer>

                </v-card-title>

                <v-simple-table fixed-header height="100%">
                    <template v-slot:default>
                        <thead style="overflow-y: scroll;overflow-x: hidden;">
                            <tr>
                                <th class="text-left">Route</th>
                                <th class="text-left">Notes</th>
                                <th class="text-left">location</th>
                                <th class="text-left">Date Time</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="item in addressData" :key="item.datetime" @click="selectedIncident(item)">
                                <td>{{ item.route }}</td>

                                <td>{{ item.note }}</td>

                                <td>
                                    <v-btn icon @click="mapScreen(item)">
                                        <v-icon color="green">location_on</v-icon>
                                    </v-btn>
                                </td>

                                <td>{{ new Date(item.datetime).toLocaleString('en-GB') }}</td>
                            </tr>
                        </tbody>
                    </template>
                </v-simple-table>
            </v-card>

        </v-col>
        <v-col cols="12" md="4">

            <v-card hover class="mb-12">
                <v-card-title class="headline blue-grey lighten-1 white--text" primary-title>
                    Incident Image
                    <v-spacer></v-spacer>
                </v-card-title>
                <v-img :src="imageData"></v-img>

            </v-card>

        </v-col>

    </v-row>

    <v-dialog v-model="dialogEditItem" width="500">

        <v-card>
            <v-card-title class="headline grey lighten-2" primary-title>
                User Selection

            </v-card-title>
            <v-flex ma-3>
                <v-combobox v-model="selectedUser" :items="users" item-text="userName" hide-selected label="Select user type"></v-combobox>

                <v-btn @click="assign" large block color="primary">Assign User to route</v-btn>
            </v-flex>
            <v-card-text>

            </v-card-text>

        </v-card>
    </v-dialog>

    <v-dialog v-model="dialogloader" hide-overlay persistent width="300">
        <v-card color="primary" dark>
            <v-card-text>
                <br>
                <h3>Please stand by</h3>
                <br>
                <v-progress-linear indeterminate color="white" class="mb-0"></v-progress-linear>
                <br>
            </v-card-text>
        </v-card>
    </v-dialog>

    <v-dialog v-model="dialogMap" fullscreen hide-overlay transition="dialog-bottom-transition">

        <v-card>
            <v-toolbar dark color="#2B5563">
                <v-toolbar-title>Addresses on routes</v-toolbar-title>
                <v-spacer></v-spacer>
                <v-toolbar-items>
                    <v-btn dark text @click="dialogMap = false">Close</v-btn>
                </v-toolbar-items>
            </v-toolbar>

            <v-flex xs12 lg12>
                <div class="map">
                    <MapScreen :mapData="geoData" />
                </div>
            </v-flex>

        </v-card>
    </v-dialog>
</v-flex>
</template>

<script>
import MapScreen from './Map2'
import Axios from 'axios-observable'
export default {
    components: {
        MapScreen
    },
    data() {
        return {
            e1: 0,
            select: null,
            geoData: null,
            selectedUser: null,
            dialogEditItem: false,
            users: [],
            dialogloader: false,
            nodeApi: '',
            selectedRoute: null,
            selectedItem: null,
            routes: [],
            addressData: [],
            editedIndex: -1,
            imageData: '',
            dialogMap: false
        }
    },
    watch: {
        dialogEditItem(val) {
            if (val) {
                this.getUsers()
            } else {
                this.editedIndex = -1
            }
        }
    },

    methods: {
        mapScreen(item) {
            // console.log(item)
            let incident = [{
                lat: item.lat,
                log: item.log,
                dateTime: item.datetime,
                address: item.note,
                clientName: item.route
            }]
            this.geoData = incident
            this.dialogMap = true
        },
        selectedIncident(data) {
            this.imageData = `data:image/png;base64,${data.image}`
        },
        selectedRowItem(data) {
            // this.selectedItem = data.route
            // let temp = JSON.parse(data.data)
            this.addressData = JSON.parse(data.data)
            //console.log(JSON.parse(this.addressData))
        },
        openAssigner(data) {
            this.selectedRoute = data
            this.selectedRoute.address = JSON.stringify(this.selectedRoute.address)
            this.dialogEditItem = true
        },
        async getUsers() {
            let headers = {
                'Content-Type': 'application/json',
                Authorization: 'Bearer ' + this.$session.get('user').token
            }
            Axios.get(this.nodeApi + '/getAllUsers', {
                headers: headers
            }).subscribe(
                res => {
                    this.users = res.data
                },
                err => console.log(err)
            )
        },
        async assign() {
            this.getRoutes()
            this.dialogEditItem = false
        },
        async getRoutes() {
            let headers = {
                'Content-Type': 'application/json',
                Authorization: 'Bearer ' + this.$session.get('user').token
            }
            var token = this.$jwt.sign({
                assignTo: this.selectedUser.id
            }, 'meterlink@@11', {
                expiresIn: 15
            })
            let obj = {
                payload: token
            }
            Axios.post(this.nodeApi + '/getAllIncidentsByUser', obj, {
                headers: headers
            }).subscribe(
                res => {
                    var decoded = this.$jwt.decode(res.data.token, {
                        complete: true
                    })
                    this.routes = decoded.payload.rows
                    //console.log(this.routes)
                },
                err => console.log(err)
            )
        },
        loadRoutes() {
            this.getRoutes()
        },

        updateItem() {
            if (this.editedIndex > -1) {
                Object.assign(this.itemData[this.editedIndex], this.newItem)
                this.dialogEditItem = false
            } else {
                this.itemData.push({
                    quantity: this.newItem.quantity,
                    item: this.newItem.item,
                    unitprice: this.newItem.unitprice
                })
                this.dialogEditItem = false
            }
        },
        editItem(item) {
            this.editedIndex = this.itemData.indexOf(item)
            this.newItem = Object.assign({}, item)
            this.dialogEditItem = true
        },
        deleteItem(item) {
            const index = this.routes.indexOf(item)
            this.routes.splice(index, 1)
        }
    }
}
</script>

<style scoped>
.map {
    height: 90vh;
}

.block {
    height: 50%;
}

.container {
    padding-left: 15%;
    padding-right: 15%;
}

.inputfield {
    width: 70%;
    height: 50px;
    text-align: end;
    font-size: 20px;
}

.inputfield2 {
    width: 70%;
    height: 50px;
    text-align: end;
    font-size: 15px;
}

.my-custom-scrollbar {
    position: relative;
    height: 780px;
    width: auto;
    overflow-y: scroll;
    overflow-x: hidden;
}

.scroll-y {
    display: block;
}
</style>
